@charset "UTF-8";
.progress-time:hover {
  color: #fff;
}
.subject-title-box {
  padding: 15px;
}
.subject-title-box .answer-progress {
  background: #e5e5e5;
  float: left;
  padding: 0 5px;
  border-radius: 5px;
  font-size: 16px;
}
.subject-title-box .subject-title {
  background-position: 12px 0;
  margin-left: 40px;
  padding: 0 20px 0 45px;
  border-bottom: none;
}
.subject-new-tips {
  clear: both;
  margin-top: 10px;
  color: #888;
  font-size: 14px;
  text-align: right;
}
.answer-sheet-type {
  float: right;
  font-size: 14px;
  color: #666;
  background: #f2f2f2;
  padding: 5px 10px;
}
.answer-sheet-type > span + span {
  border-left: 2px solid #aaa;
  padding-left: 15px;
  margin-left: 15px;
}
.com-subject-wrap {
  padding: 20px;
  border-bottom: 1px solid #eee;
}
.com-subject-wrap .com-subject-title {
  font-size: 20px;
  display: inline-block;
  vertical-align: middle;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
  word-wrap: normal;
  max-width: 25em;
}
.com-subject-wrap .progress-time {
  background: 0 0;
  color: #333;
  padding-top: 20px;
}
.strong {
  font-weight: 700;
}
.clearfix {
  display: block;
  zoom: 1;
}
.clearfix:after,
.clearfix:before {
  clear: both;
  content: ".";
  display: block;
  height: 0;
}
.fs {
  font-size: 14px;
}
.m-1 {
  margin: 5px !important;
}
.nk-main {
  margin: 0 auto;
  width: 1000px;
}
.module-box {
  border: 1px solid #e0e0e0;
  border-radius: 3px;
  margin-bottom: 10px;
  background: #fff;
}
.subject-box {
  margin-bottom: 20px;
  border: 1px solid #e0e0e0;
  border-radius: 3px;
  position: relative;
}
.subject-title {
  padding: 15px 20px 15px 55px;

  line-height: 1.5;
  font-size: 16px;
  border-bottom: 1px solid #eee;
  border-radius: 3px;
}
.subject-title {
  font-size: 12px;
  margin-top: 10px;
}
.progress-time {
  font-size: 20px;
  position: absolute;
  right: 0;
  top: 0;
  background: #2d3339;
  border-radius: 0 6px 0 0;
  color: #fff;
  height: 40px;
  width: 250px;
  padding-top: 10px;
  text-align: center;
}
.subject-main {
  background: #fff;
  padding: 20px;
}
.subject-main:after,
.subject-main:before {
  content: " ";
  clear: both;
  content: ".";
  display: block;
  height: 0;
  visibility: hidden;
}
.subject-main img {
  max-width: 100%;
}
.subject-main .checkbox,
.subject-main .radio {
  margin: 0;
  line-height: 20px;
}
.subject-content {
  position: relative;
  margin-bottom: 20px;
}
.subject-question {
  font-size: 14px;
  line-height: 1.6;
  margin-bottom: 20px;
}
.subject-question ul {
  list-style: disc;
  padding-left: 30px;
  margin: 10px 0;
}
.subject-question ol {
  list-style: decimal;
  padding-left: 30px;
  margin: 10px 0;
}
.subject-question img {
  _width: 100%;
  max-width: 100%;
}
.subject-action {
  margin-bottom: 20px;
  font-size: 14px;
}
.subject-action .subject-next {
  float: right;
}
.subject-action .btn {
  margin-right: 10px;
}
.btn-primary.disabled {
  background: #bac4c5;
}
.subject-opr {
  float: left;
  font-size: 14px;
  line-height: 20px;
  margin-top: 13px;
}
.subject-opr-item {
  margin: 0 15px;
}
.subject-opr-item i {
  display: inline-block;
  width: 16px;
  height: 16px;
  margin-right: 5px;
  vertical-align: text-bottom;
}
.subject-opr-item .ico-collect {
  background-position: -79px 0;
}
.subject-opr-item:hover .ico-collect {
  background-position: -111px -16px;
}
.answer-sheet-box {
  border-top: 1px dashed #ddd;
  padding: 20px;
  font-size: 16px;
  background: #fff;
}
.card-fold,
.card-unfold {
  background: url(https://static.nowcoder.com/images/img/fold-ico.png) right 6px
    no-repeat;
  display: inline-block;
  *zoom: 1;
  *display: inline;
  padding-right: 20px;
}
.card-unfold {
  background: url(https://static.nowcoder.com/images/img/foldArrow.png) right -6px
    no-repeat;
  display: none;
}
.open .card-unfold {
  display: inline-block;
  *zoom: 1;
  *display: inline;
}
.open .card-fold {
  display: none;
}
.answer-sheet-num {
  margin-top: 15px;
  display: none;
}
.answer-sheet-num li {
  float: left;
  margin-right: 10px;
}
.answer-sheet-num a {
  position: relative;
  display: block;
  color: #dce4ec;
  font-size: 16px;
  width: 40px;
  text-align: center;
  height: 40px;
  line-height: 40px;
}
.answer-sheet-num a.answer-done {
  color: #59b59c;
}
.answer-sheet-num a.answering-num,
.answer-sheet-num a:hover {
  display: block;
  background: #59b59c;
  color: #fff;
}
.open .answer-sheet-num {
  display: block;
}
.nowcoder-topic {
  background: #fff;
}
.container {
  background: #f7f8f9;
  position: relative;
  height: auto;
  min-height: 100%;
}
.nowcoder-header {
  background: #3d444c;
  position: fixed;
  width: 100%;
  z-index: 101;
  left: 0;
  margin-bottom: 30px;
}
.header-main {
  width: 1200px;
  height: 54px;
  margin: 0 auto;
}
